import PerfectScrollbar from 'perfect-scrollbar';

$('.js-main-menu')
	.each((index, component) => {
		let $body = $(document.body);
		let $component = $(component);
		let $closeBtn = $component.find('.js-close-menu');
		let $contentContainer = $component.find('.js-main-menu-content');
		let $section = $component.find('.js-main-menu-section');
		let $listBtn = $component.find('.js-menu-list-btn');
		let $list = $component.find('.js-menu-list');
		let ps = [];

		$section.each((sectionIndex, item) => {
			ps[index] = new PerfectScrollbar($(item)[0], {
				suppressScrollX: true,
			});
		});

		function hideLists() {
			$section.removeClass('main-menu__section_active');
			$listBtn.removeClass('menu-list__btn_active');
			$list.removeClass('menu-list_active');
			$contentContainer
				.removeClass('main-menu__content_active');
		}

		function showSection(sectionId, listId) {
			let $currentSection = $section.eq(sectionId);
			let $sectionLists = $currentSection.find('.js-menu-list');
			let $currentList = $sectionLists.eq(listId);

			$currentSection.find('.js-menu-list-btn')
				.removeClass('menu-list__btn_active');

			if (sectionId === 2) {
				$contentContainer
					.addClass('main-menu__content_active');
			} else {
				$contentContainer
					.removeClass('main-menu__content_active');
			}

			$section.eq(sectionId + 1)
				.removeClass('main-menu__section_active');

			$currentSection.addClass('main-menu__section_active');
			$sectionLists.removeClass('menu-list_active');
			$currentList.addClass('menu-list_active');

			ps.forEach((item) => item.update());
		}

		$listBtn.on('click', (e) => {
			e.preventDefault();

			let $trgt = $(e.target);
			let itemRequestId = $trgt.data('request-id')
				.split('-')
				.map(Number);

			if ($trgt.hasClass('menu-list__btn_active')) {
				window.location = e.target.href;
			}

			let $sectionBtns = $trgt.closest('.js-main-menu-section')
				.find('.js-menu-list-btn');

			$sectionBtns.removeClass('menu-list__btn_active');
			$trgt.addClass('menu-list__btn_active');

			showSection(itemRequestId[0], itemRequestId[1]);
		});

		$body.on('main-menu:open', () => {
			$component.addClass('main-menu_active');
		});

		$body.on('main-menu:close', () => {
			$component.removeClass('main-menu_active');

			hideLists();
		});

		$closeBtn.on('click', (e) => {
			e.preventDefault();

			$body.trigger('main-menu:close');
		});
	});
