import Swiper from 'swiper';

$('.js-material-slider')
	.each((index, component) => {
		let $component = $(component);
		let $slider = $component.find('.swiper-container');
		let slidesLength = $component.find('.swiper-slide').length;
		let sliderOptions = {
			slidesPerView: 3.5,
			scrollbar: {
				el: '.swiper-scrollbar',
				hide: true,
			},
			navigation: {
				nextEl: '.material-slider__button_next',
				prevEl: '.material-slider__button_prev',
				disabledClass: 'material-slider__button_disabled',
			},
		};

		if (slidesLength > sliderOptions.slidesPerView) {
			let swiper = new Swiper($slider.eq(0), sliderOptions);

			swiper.on('fromEdge', () => {
				$slider.stop(true, true)
					.addClass('material-slider__container_has-before material-slider__container_has-after');
			});

			swiper.on('reachBeginning', () => {
				$slider.stop(true, true)
					.removeClass('material-slider__container_has-before');
			});

			swiper.on('reachEnd', () => {
				$slider.stop(true, true)
					.removeClass('material-slider__container_has-after');
			});

			$(document.body)
				.on('collapse-show', () => {
					swiper.update();
				});

			$(document.body).on('update-sliders', () => {
				swiper.update();
			});
		} else {
			$component.addClass('material-slider_not-slider');
		}
	});
