import Swiper from 'swiper';

$('.js-gallery-slider')
	.each((index, component) => {
		let $component = $(component);
		let $slider = $component.find('.swiper-container');
		let sliderOptions = {
			slidesPerView: 'auto',
			spaceBetween: 48,
			scrollbar: {
				el: '.swiper-scrollbar',
				hide: true,
			},
			navigation: {
				nextEl: '.gallery-slider__button_next',
				prevEl: '.gallery-slider__button_prev',
				disabledClass: 'gallery-slider__button_disabled',
			},
		};
		let swiper = new Swiper($slider.eq(0), sliderOptions);

		swiper.on('fromEdge', () => {
			$slider.stop(true, true)
				.addClass('gallery-slider__container_has-before gallery-slider__container_has-after');
		});

		swiper.on('reachBeginning', () => {
			$slider.stop(true, true)
				.removeClass('gallery-slider__container_has-before');
		});

		swiper.on('reachEnd', () => {
			$slider.stop(true, true)
				.removeClass('gallery-slider__container_has-after');
		});

		$(document.body)
			.on('collapse-show', () => {
				swiper.update();
			});
	});
