$('.js-collapse-list')
	.each((listIndex, component) => {
		let $component = $(component);
		let $collapseHead = $component.find('.js-collapse-head');
		let $collapseContent = $component.find('.js-collapse-content');

		$collapseContent.collapse({
			parent: $component,
			toggle: false,
		});

		$collapseContent.on('show.bs.collapse shown.bs.collapse', () => {
			$(document.body)
				.trigger('collapse-show');
		});

		setTimeout(() => {
			$collapseContent.stop(true, true)
				.removeClass('show');
			$component.stop(true, true)
				.addClass('collapse-list_show');
		}, 30);

		$collapseHead.on('click', (e) => {
			e.preventDefault();

			let $trgt = $(e.currentTarget);

			if ($trgt.hasClass('collapse-head_active')) {
				$collapseHead.stop(true, true)
					.removeClass('collapse-head_active');

				$(e.currentTarget)
					.closest('.js-collapse-item')
					.stop(true, true)
					.removeClass('collapse-item_active');

				$(e.currentTarget)
					.closest('.js-collapse-item')
					.find('.js-collapse-content')
					.stop(true, true)
					.collapse('hide');
			} else {
				$collapseHead.stop(true, true)
					.removeClass('collapse-head_active');

				$(e.currentTarget)
					.stop(true, true)
					.addClass('collapse-head_active');

				$(e.currentTarget)
					.closest('.js-collapse-item')
					.find('.js-collapse-content')
					.stop(true, true)
					.collapse('show');
			}
		});
	});
