import '@fancyapps/fancybox';
import createVideoJs from './createVideoJs';
import PerfectScrollbar from 'perfect-scrollbar';

let ps = null;
let thumbsPs = null;

$('[data-fancybox]')
	.fancybox({
		buttons: [
			'zoom',
		],
		backFocus: false,
		modal: false,
		infobar: true,
		image: {
			preload: true,
		},
		baseTpl:
			`<div class="fancybox-container" role="dialog" tabindex="-1">
				<div class="fancybox-bg"></div>
				<div class="fancy">
					<button data-fancybox-close class="fancy__btn fancy__close-btn" title="{{CLOSE}}">
						<span class="icon icon_cross">
							<svg><use xlink:href="/images/sprites.svg#icon-cross"></use></svg>
						</span>
					</button>
					<div class="fancy__inner">
						<div class="fancy__content">
							<div class="fancybox-stage fancy__stage"></div>
							{{arrows}}
							<div class="fancy__toolbar">{{buttons}}</div>
						</div>
						<div class="fancy__caption js-caption">
						</div>
					</div>
					<div class="fancy__infobar">
						<span data-fancybox-index></span>&nbsp;/&nbsp;<span data-fancybox-count></span>
					</div>
				</div>
				<div class="fancy__thumbs"></div>
			</div>`,
		video: {
			tpl:
				`<div class="fancybox-content">
					<div class="fancy__video">
						<video class="video video-js js-video" controls controlsList="nodownload" poster="{{poster}}">
							<source src="{{src}}" type="{{format}}" />
						</video>
					</div>
				</div>`,
			format: '',
			autoStart: false,
		},
		btnTpl: {
			zoom:
				`<button data-fancybox-zoom class="fancy__btn" title="{{ZOOM}}">
					<span class="icon icon_zoom">
						<svg><use xlink:href="/images/sprites.svg#icon-zoom"></use></svg>
					</span>
				</button>`,

			// Arrows
			arrowLeft:
				`<button data-fancybox-prev class="fancy__arrow fancy__arrow_left fancy__btn" title="{{PREV}}">
					<svg><use xlink:href="/images/sprites.svg#icon-angle-left-thin"></use></svg>
				</button>`,

			arrowRight:
				`<button data-fancybox-next class="fancy__arrow fancy__arrow_right fancy__btn" title="{{NEXT}}">
					<svg><use xlink:href="/images/sprites.svg#icon-angle-right-thin"></use></svg>
				</button>`,
		},
		thumbs: {
			autoStart: true,
			hideOnClose: true,
			parentEl: '.fancy__thumbs',
			axis: 'x',
		},
		onInit: function (instance, current) {
			let $fancyBox = $('.fancybox-container');

			if (!ps) {
				ps = new PerfectScrollbar($fancyBox.find('.js-caption')[0], {
					suppressScrollX: true,
				});
			}
		},
		beforeLoad: function(instance, current) {
			let $fancyBox = $('.fancybox-container');
			let $fancyThumbs = $fancyBox.find('.fancy__thumbs .fancybox-thumbs');

			if ($fancyThumbs.length && !thumbsPs) {
				thumbsPs = new PerfectScrollbar($fancyThumbs[0], {
					suppressScrollY: true,
					swipeEasing: true,
				});
			}
		},
		afterLoad: function (instance, current) {
			let $currentContent = $(current.$content);

			if (current.type === 'video') {
				let $video = $currentContent.find('.js-video');

				if ($(current.opts.$orig)
					.data('audio')) {
					$video.addClass('video_audio');
				}

				createVideoJs($video[0]);
			}
		},
		afterShow: function (instance, current) {
			let $clickedElement = $(current.opts.$orig);
			let currentCaption = ($clickedElement.data('caption') || '') +
				($clickedElement
					.find('.js-data-caption')
					.html() || '');

			let $fancyBox = $('.fancybox-container');
			let $captionContainer = $fancyBox.find('.js-caption');

			$captionContainer.html(currentCaption);

			$captionContainer.find('.js-video')
				.each((index, element) => {
					if ($(element)
						.data('audio')) {
						$(element)
							.addClass('video_audio');
					}
					createVideoJs($(element)[0]);
				});

			if (ps) {
				ps.update();
			}

			if (thumbsPs) {
				thumbsPs.update();
			}
		},
		afterClose: function () {
			if (ps) {
				ps.destroy();
				ps = null;
			}

			if (thumbsPs) {
				thumbsPs.destroy();
				thumbsPs = null;
			}
		},
	});
