$('.js-standstill').each((index, component) => {
	let $component = $(component);
	let standstillTimeout = setTimeout(() => {
		$component.fadeIn(300);
	}, 420000);

	let mainPageTimeout = setTimeout(() => {
		window.location = '/';
	}, 450000);

	$component.on('click', () => {
		$component.stop(true, true).fadeOut(300);

		if (standstillTimeout) {
			clearTimeout(standstillTimeout);
		}

		if (mainPageTimeout) {
			clearTimeout(mainPageTimeout);
		}
	});

	$(document.body).on('click', () => {
		if (standstillTimeout) {
			clearTimeout(standstillTimeout);
		}

		if (mainPageTimeout) {
			clearTimeout(mainPageTimeout);
		}

		standstillTimeout = setTimeout(() => {
			$component.fadeIn(300);
		}, 420000);

		mainPageTimeout = setTimeout(() => {
			window.location = '/';
		}, 450000);
	});
});
